.App-content {
	-ms-flex: 1 0 auto;
	    flex: 1 0 auto;
	/*background-color: #328cc1;*/
	background-image: -webkit-gradient(linear, left top, right bottom, from(#25678e), to(#9ac9e5));
	background-image: -webkit-linear-gradient(top left, #25678e, #9ac9e5);
	background-image: -o-linear-gradient(top left, #25678e, #9ac9e5);
	background-image: linear-gradient(to bottom right, #25678e, #9ac9e5);
	padding: 40px;
	padding-bottom: 0px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-wrap: nowrap;
	    flex-wrap: nowrap;
	border-top: 1px solid black;
	border-bottom: 1px solid black;
}

@media only screen and (max-width: 1000px) {
	.App-content {
		padding: 0.5rem;
	}
}



