.List-box {
  width: 920px;
  height: auto;
  max-height: 600px;
  margin-bottom: 20px;
  /*border: 1px solid black;*/
  /*border-radius: 20px;*/
  overflow: auto;
}

.List-box table {
  /*border-collapse: collapse;*/
  width: 100%;
}

.List-box tbody {
}

th,
td {
  /*border: 1px solid black;*/
  background-color: rgba(255, 255, 255, 0.5);
}

.Th-spacing,
.Td-spacing {
  border: none;
  /*border-left: 1px solid black;*/
  background-image: none !important;
  background-color: rgba(255, 255, 255, 0) !important;
}

.List-box th {
  /*background-color: #e6e6e6;*/
  /*background-image: linear-gradient(#bfbfbf, #f2f2f2);*/
  /*background-color: #ff8000;*/
  border-top: none;
  background-image: -webkit-gradient(linear, left top, left bottom, from(#ff8000), to(#e67300));
  background-image: -webkit-linear-gradient(#ff8000, #e67300);
  background-image: -o-linear-gradient(#ff8000, #e67300);
  background-image: linear-gradient(#ff8000, #e67300);
  padding: 8px;
  font-family: "Lato", sans-serif;
  color: white;
  text-shadow: 0.5px 0.5px 0px rgba(0, 0, 0, 1);
}

.List-box td {
  font-family: "PT Sans", sans-serif;
  white-space: wrap;
  overflow: auto;
  -o-text-overflow: clip;
     text-overflow: clip;
  font-size: 1rem;
  padding: 3px;
  letter-spacing: 0.5px;
}

.Th-id,
.Td-id {
  width: 5%;
  border-left: none;
}

.Td-id {
  text-align: center;
}

.Th-buttons,
.Td-buttons {
  width: 10%;
  border-right: none;
}

.Th-name,
.Td-name,
.Th-surname,
.Td-surname {
  width: 18%;
}

.Th-address,
.Td-address {
  width: 44%;
}

.Td-name,
.Td-surname,
.Td-address {
  padding-left: 10px !important;
  padding-bottom: 0px !important;
}

.Th-gender,
.Td-gender {
  width: 5%;
  text-align: center;
}

.Td-buttons-wrapper {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}

.List-button {
  width: 25px;
  height: 23px;
  line-height: 20px;
  font-size: 0.8rem;
  padding: 0;
  /*background-color: #328cc1;*/
  background-image: -webkit-gradient(linear, left top, right bottom, from(#328cc1), to(#1a4a65));
  background-image: -webkit-linear-gradient(top left, #328cc1, #1a4a65);
  background-image: -o-linear-gradient(top left, #328cc1, #1a4a65);
  background-image: linear-gradient(to bottom right, #328cc1, #1a4a65);
  border-color: #328cc1;
  outline: none;
  border-radius: 5px;
  color: white;
}

.List-button:hover {
  /*background-color: #0b3c5d;*/
  background-image: -webkit-gradient(linear, left top, right bottom, from(#1a4a65), to(#328cc1));
  background-image: -webkit-linear-gradient(top left, #1a4a65, #328cc1);
  background-image: -o-linear-gradient(top left, #1a4a65, #328cc1);
  background-image: linear-gradient(to bottom right, #1a4a65, #328cc1);
}

.Th-shadow-box {
  height: 10px;
  background-image: -webkit-gradient(linear, left top, left bottom, from(#050505), to(#262626));
  background-image: -webkit-linear-gradient(#050505, #262626);
  background-image: -o-linear-gradient(#050505, #262626);
  background-image: linear-gradient(#050505, #262626);
}

@media only screen and (max-width: 1000px) {
  .List-box {
    width: 100%;
    height: 0px;
    min-height: 300px;
    font-size: 1rem;
  }

  table,
  th,
  td {
    border-spacing: 1px 2px;
  }

  .List-box th {
    font-size: 0.725rem;
    padding: 0.1rem !important;
  }

  .List-box td {
    font-size: 0.725rem;
    padding: 0.1rem !important;
  }

  .List-button {
    width: 20px;
    height: 20px;
    line-height: 16px;
    font-size: 0.6rem;
    margin: 1px;
  }
}



